.tag {
  border-radius: 0.7em;
  padding: 6px 8px 7px;
  margin-right: .8rem;
  line-height: 3rem;
  letter-spacing: 0;
  border: 1px solid #dee2e6 !important;
  box-shadow: 0 0 3px 0 #e9ecef;
}

.tag span {
  margin-left: 0.6em;
  font-size: 0.7em;
  font-family: 'Oswald', sans-serif;
}

a.tag:hover {
  text-decoration: none;
}